<template>
    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true"
        ref="modal">
        <!--  v-model -->
        <div class="modal-dialog modal-xl" role="document">
            <div class="modal-content border-0">
                <div class="modal-header bg-dark text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                        <span></span>
                    </h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="mb-3">
                                <label for="image" class="form-label"></label>
                                <input type="text" class="form-control" id="image" placeholder="" v-model="this.tempProduct.imageUrl">
                            </div>
                            <div class="mb-3">
                                <label for="customFile" class="form-label"> 
                                    <i class="fas fa-spinner fa-spin"></i>
                                </label>
                                <input type="file" id="customFile" class="form-control" @change="uploadFile" ref="fileinput">
                            </div>
                            <img class="img-fluid" alt="" v-for=" (items,index) in this.tempProduct.imagesUrl" :key ="index"  :src="items">
                            <!--  -->
                            <div class="mt-5">
                                <div class="mb-3 input-group">
                                    <input type="url" class="form-control form-control" placeholder="">
                                    <button type="button" class="btn btn-outline-danger">
                                        
                                    </button>
                                </div>
                                <div>
                                    <button class="btn btn-outline-primary btn-sm d-block w-100">
                                        
                                    </button>
                                </div>
                            </div>
                        </div>
                        <div class="col-sm-8">
                            <div class="mb-3">
                                <label for="title" class="form-label"></label>
                                <input type="text" class="form-control" id="title" placeholder="" v-model="this.tempProduct.title">
                            </div>

                            <div class="row gx-2">
                                <div class="mb-3 col-md-6">
                                    <label for="category" class="form-label"></label>
                                    <input type="text" class="form-control" id="category" placeholder="" v-model="this.tempProduct.category">
                                </div>
                                <div class="mb-3 col-md-6">
                                    <label for="price" class="form-label"></label>
                                    <input type="text" class="form-control" id="unit" placeholder="" v-model="this.tempProduct.unit">
                                </div>
                            </div>

                            <div class="row gx-2">
                                <div class="mb-3 col-md-6">
                                    <label for="origin_price" class="form-label"></label>
                                    <input type="number" class="form-control" id="origin_price" placeholder="" v-model="this.tempProduct.origin_price">
                                </div>
                                <div class="mb-3 col-md-6">
                                    <label for="price" class="form-label" ></label>
                                    <input type="number" class="form-control" id="price" placeholder="" v-model="this.tempProduct.price">
                                </div>
                            </div>
                            <hr>

                            <div class="mb-3">
                                <label for="description" class="form-label"></label>
                                <textarea type="text" class="form-control" id="description"
                                    placeholder="" v-model="this.tempProduct.description"></textarea>
                            </div>
                            <div class="mb-3">
                                <label for="content" class="form-label"></label>
                                <textarea type="text" class="form-control" id="content"
                                    placeholder="" v-model="this.tempProduct.content"></textarea>
                            </div>
                            <div class="mb-3">
                                <div class="form-check">
                                    <input class="form-check-input" type="checkbox" :true-value="1" :false-value="0"
                                        id="is_enabled" v-model="this.tempProduct.is_enabled">
                                    <label class="form-check-label" for="is_enabled">
                                        
                                    </label>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-bs-dismiss="modal">
                    </button>
                    <button type="button" class="btn btn-primary" @click.prevent="$emit('updata-product',tempProduct)"></button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import modalMixin from '@/Mixin/modalMixin';
export default {
    props: {
        product: {
            type: Object,
            defaule () {
                return {};
            }
        }
    },
    watch: {
        product () {
            this.tempProduct = this.product;
        }
    },
    data () {
        return {
            modal: {},
            tempProduct: {}
        };
    },
    mixins: [modalMixin],
    methods: {
        uploadFile () {
            const uploadFile = this.$refs.fileinput.files[0];
            const formdata = new FormData();
            formdata.append('file-to-upload', uploadFile);
            const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`;
                this.$http.post(api, formdata)
                .then((res) => {
                    if (res.data.success) {
                        this.tempProduct.imageUrl = res.data.imageUrl;
                    }
                });
        }
    }
};
</script>
